body {
  font-family: "Poppins", sans-serif;
}
body p {
  font-size: 15px;
}
body.logged-in header {
  @apply top-6;
}

.single p {
  margin-bottom: 20px;
}

::selection {
  @apply bg-violet-100 text-violet-600;
}

.transition-fade {
  transition: 0.4s;
  opacity: 1;
}

html.is-animating .transition-fade {
  opacity: 0;
}

.swup-progress-bar {
  height: 2px;
  @apply bg-violet-800 z-20;
}

#header .menu-item {
  font-family: "Sarabun", sans-serif;
  font-weight: 400;
  font-size: 14px;
}

h2 {
  @apply font-extrabold text-5xl text-gray-300;
}

.secondary-font {
  font-family: "Sarabun", sans-serif;
}

.btn-primary {
  @apply relative flex items-center z-10 px-5 pl-4 pr-6;
}
.btn-primary::before {
  content: "";
  @apply absolute top-1/2 left-0 bg-violet-400 rounded-full -z-10 -translate-y-2/4;
  width: 52px;
  height: 52px;
  transition: 0.4s;
}
.btn-primary span {
  @apply text-white uppercase text-sm;
  font-family: "Sarabun", sans-serif;
  letter-spacing: 1px;
}
.btn-primary svg {
  @apply w-4 h-4 text-white transition ml-1;
}
.btn-primary:hover::before {
  @apply bg-violet-600;
  width: 100%;
}
.btn-primary:hover svg {
  @apply translate-x-2;
}

#hero {
  position: relative;
}
#hero .subtitle {
  letter-spacing: 14px;
  font-size: 18px;
}

pre {
  background: #1f2938 !important;
  border-radius: 10px;
  margin: 3rem 0 !important;
  font-size: 13px !important;
}
pre .token.comment {
  color: #8d8d8d;
}
pre .token.keyword {
  color: #c990cc;
}
pre .token.function-definition {
  color: #558ee4;
}
pre .token.function:not(.function-definition) {
  color: #fbe977;
}
pre .token.variable {
  color: #f1f1f1;
}
pre .token.string {
  color: #ffc086;
}